.detailNav {
  width: 300px;
}
.homeNav {
  width: 200px;
  padding: 20px;
  text-align: center;
  background-color: rgb(245 245 245);
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  position: relative;
  height: 130px;
  overflow: hidden;
}
.homeNav h1 {
  font-size: 48px;
  text-align: center;
  margin-bottom: 20px;
}
.homeNav h1 img {
  margin: 0 auto;
  width: 40px;
}
.homeNav .name p {
  font-size: 18px;
}
.detailDesc {
  color: #888;
  font-size: 14px;
  margin-top: 5px;
  text-align: left;
  display: none;
}

.homeNav:hover {
  width: 400px;
}

.homeNav:hover .name {
  display: flex;
  font-weight: bold;
  align-items: center;
}
.homeNav:hover .name h1 {
  font-size: 18px;
  margin-right: 10px;
  margin-bottom: 0;
}
.homeNav:hover .name h1 img {
  width: 20px;
}

.homeNav:hover .detailDesc {
  display: block;
}

.navBtn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  opacity: 0;
}

.homeNav:hover .navBtn {
  opacity: 1;
}
